import { Component, OnInit } from '@angular/core';
import { PasseioService } from '../../services/passeio.service'
import { Geolocation } from '@ionic-native/geolocation/ngx'
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-detalhes',
  templateUrl: './detalhes.page.html',
  styleUrls: ['./detalhes.page.scss'],
})
export class DetalhesPage implements OnInit {

  constructor(
    private geolocation: Geolocation,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private passeioService: PasseioService
  ) { }

  public passeio: Object = {};
  public cliente: Object = {}; 
  public passeioAtivo: Boolean = false;
  public passeioFinalizado: Boolean = false;
  public latitude: any;
  public longitude: any;
  public passeioId: any;

  iniciarPasseio(){

    this.alterarStatusPasseio();
     
    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {

      this.latitude = data["coords"]["latitude"];
      this.longitude = data["coords"]["longitude"];

      this.passeioService.postLocalizacaoPasseio(
        this.latitude,
        this.longitude,
        this.passeio["id"]
      ).subscribe();

    });

  }

  finalizarPasseio(){
    this.alterarStatusPasseio();
    this.passeioFinalizado = !this.passeioFinalizado;

    this.passeioService.putAlterarStatus("Finalizado", this.passeioId).subscribe(response => {

      if(response){
        location.reload();
      }

    });

  }

  alterarStatusPasseio(){
    this.passeioAtivo = !this.passeioAtivo
  }

  ngOnInit() {

    this.activatedRoute.params.forEach(param =>
      this.passeioId = param['id']
    );

    try {
      
      this.passeioService.getDetalhes(this.passeioId).subscribe(response => {
        this.passeio = response
        this.cliente = response["cliente"]["pessoa"]
      })

    } catch (error) {
      console.log(error)
    }

  }

}
